@import url(https://fonts.googleapis.com/css2?family=Bitter&display=swap);
body {
  line-height: 1.4;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-item {
  width: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-button {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #484a4d;
  padding: 10px 10px;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
}

.icon-button:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}
.share {
  width: 135px;
  height: 40px;
  position: fixed;
  top: 60%;
  margin-top: 40px;
  left: 26%;
  font-weight: 400;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  padding: 10px 20px;
  font-size: 20;
  background-color: rgb(209, 209, 209);
  overflow: hidden;
  outline: none;
}

.dropdown {
  padding-top: 10px;
  width: 135px;
  top: 60%;
  left: 26%;
  margin-top: 70px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  position: absolute;
  background-color: rgb(209, 209, 209);
  z-index: -1;
}

.menu-item {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  transition: background 300ms;
  padding: 0.5rem;
}

.menu-item:hover {
  background-color: #525357;
}

.nextbtn {
  width: 135px;
  height: 40px;
  position: fixed;
  top: 60%;
  left: 51%;
  margin-top: 40px;
  font-weight: 400;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  padding: 10px 20px;
  font-size: 16px;
  background-color: rgb(209, 209, 209);
  overflow: hidden;
  outline: none;
}

.nextbtn::before,
.share::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0%;
  background: #333;
  z-index: -1;
  transition: 0.5s;
  top: 0;
  border-radius: 0 0 50% 50%;
  outline: none;
}

.nextbtn:hover::before,
.share:hover::before {
  height: 180%;
  outline: none;
}

.nextbtn:hover,
.share:hover {
  color: #fff;
  outline: none;
}

.nextbtn:active,
.share:active {
  border: none;
}

a {
  color: inherit;
  text-decoration: none;
}

a:link,
a:visited {
  color: inherit;
}
a:hover {
  color: inherit;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Bitter", serif;
  font-size: 1rem;
}

.quote-box {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  border-radius: 3px;
  text-align: center;
  width: 40%;
  padding: 100px 0;
  background-color: rgb(233, 229, 229);
  align-items: center;
}

.quote {
  margin-top: 0px;
  padding-top: 0px;
  margin: auto;

  font-size: 1.1rem;
  width: 80%;
}

.author {
  margin: auto;
}

